<template>
  <div class="home">
    <header>
      <div class="search">
        <img class="search_img1" src="../assets/images/icon_vip@2x.png" alt />
        <input type="text" @focus="onSearch()">
        <img
          @click="setCollectShow=!setCollectShow"
          class="search_img2"
          src="../assets/images/icon_more1@2x.png"
          alt
        />
        <img class="search_img3" src="../assets/images/icon_search@2x.png" alt />
        <div class="time">{{new Date().getDate()}}</div>
        <ul v-show="setCollectShow">
          <p></p>
          <li @click="onSetCollect">
            <img src="../assets/images/essay_btn_collect_gray@2x.png" alt="">
            <span></span>
          </li>
        </ul>
      </div>
      <div class="swiper">
        <van-swipe :autoplay="3000" indicator-color="#fff">
          <van-swipe-item v-for="(item,index) of headSwiper" :key="index">
            <a :href="item.imagebarurl">
              <img :src="item.icon" alt />
            </a>
          </van-swipe-item>
        </van-swipe>
      </div>
    </header>
    <nav>
      <div class="nav">
        <transition enter-active-class="animated fadeInDown">
          <div class="attention_box" v-show="foldFlag">
            <div class="attention" @click="onSwitchover()">
              <img
                :src="navAttention==true?require('../assets/images/icon_follow@2x.png'):require('../assets/images/icon_history@2x.png')"
                alt
              />
            </div>
          </div>
        </transition>
        <transition enter-active-class="animated fadeInDown">
          <div class="nav_tab1" v-show="foldFlag">
            <div class="person-wrap" ref="personWrap">
              <ul class="person-list" ref="personTab">
                <li
                  ref="personItem"
                  v-for="(item,index) of navAttention==true?navAttentionArr:navFootprintArr"
                  :key="index"
                >
                  <a :href="item.url">
                    <div>
                      <img :src="item.icon" alt />
                    </div>
                    <p>{{item.shortname}}</p>
                    <em v-show="item.updateShow"></em>
                  </a>
                </li>
                <li ref="personItem" @click="onConcerns()">
                  <div>
                    <img src="../assets/images/icon_more@2x.png" alt />
                  </div>
                  <p></p>
                </li>
              </ul>
            </div>
          </div>
        </transition>
        <transition enter-active-class="animated fadeInUp">
          <div class="nav_tab" v-show="!foldFlag">
            <div class="title">
              <p @click="navAttention = true" :class="{nav_tab_active:navAttention==true}"></p>
              <p @click="navAttention = false" :class="{nav_tab_active:navAttention==false}"></p>
            </div>
            <ul>
              <li
                ref="nav_tab"
                v-for="(item,index) of navAttention==true?navAttentionArr:navFootprintArr"
                :key="index"
                v-show="index<7"
              >
                <a :href="item.url">
                  <div>
                    <img :src="item.icon" alt />
                  </div>
                  <p>{{item.shortname}}</p>
                  <em v-show="item.updateShow"></em>
                </a>
              </li>
              <li @click="onConcerns()">
                <div>
                  <img src="../assets/images/icon_more@2x.png" alt />
                </div>
                <p></p>
              </li>
            </ul>
          </div>
        </transition>
      </div>
      <transition enter-active-class="animated fadeInUp">
        <div class="fold" v-show="foldShow">
          <div class="triangle" @click="onNavTabView()">
            <p>{{foldFlag==true?"":""}}</p>
            <img
              :src="foldFlag==true?require('../assets/images/icon_retrac1@2x.png'):require('../assets/images/icon_retrac@2x.png')"
              alt
            />
          </div>
        </div>
      </transition>
    </nav>
    <section>
      <div class="sec_recommend">
        <div class="rec_headline">
          <div class="logo">
            <img src="../assets/images/pic_zsbb@2x.png" alt />
          </div>
          <div class="swiper">
            <van-swipe style="height: 46px;" :autoplay="3000" vertical :show-indicators="false">
              <van-swipe-item v-for="(item,index) of secSwiper" :key="index">
                <p @click="onBroadcast(item)">
                  <span>{{item.broadcasttype}}</span>
                  {{item.broadcastname}}
                </p>
              </van-swipe-item>
            </van-swipe>
          </div>
        </div>
        <div class="rec_content">
          <div class="rec_business" @click="onRecommendOne">
            <div>
              <p>{{secRecommendOne.shortcutbarname}}</p>
              <p>{{secRecommendOne.shortcutbardescript}}</p>
            </div>
            <img :src="secRecommendOne.shortcutImage" alt />
          </div>
          <div class="rec_feature" @click="onRecommendTwo">
            <p>{{secRecommendTwo.shortcutbarname}}</p>
            <img :src="secRecommendTwo.shortcutImage" alt />
          </div>
          <div class="rec_incident" @click="onRecommendThree">
            <p>{{secRecommendThree.shortcutbarname}}</p>
            <img
              :src="secRecommendThree.shortcutImage"
              alt
            />
          </div>
        </div>
      </div>
      <div class="sec_tab">
        <van-sticky :offset-top="50" z-index="200">
          <img
            class="switch_style"
            :src="tabSection==true?require('../assets/images/icon_modular@2x.png'):require('../assets/images/icon_form@2x.png')"
            alt
            @click="onSecTabView()"
          />
        </van-sticky>
        <van-tabs
          v-model="secTabActive"
          swipe-threshold="6"
          duration="0.6"
          swipeable
          sticky
          :offset-top="50"
          animated
          line-width="60"
          background="#f1f1f1"
        >
          <van-tab :title="item.modulegroupType" v-for="(item,index) of secTabContent" :key="index">
            <div class="tab_section" v-show="tabSection">
              <div class="tab_content" v-for="(item,index) of item.list" :key="index">
                <img @click="onSectionView(item)" class="img_top" :src="item.image" alt />
                <div class="title">
                  <img :src="item.icon" alt />
                  <p @click="onSectionView(item)">
                    <span>
                      {{item.title}}
                      <em v-show="item.updateShow"></em>
                    </span>
                  </p>
                </div>
                <div class="bottom">
                  <div class="bo_l">
                    <img src="../assets/images/icon_visits@2x.png" alt />
                    <p>{{item.tel}}</p>
                    <img src="../assets/images/btn_good_normal@2x1.png" alt />
                    <p>{{item.sureCount}}</p>
                  </div>
                  <ul class="bo_r" @click="item.identifySub.shadeShow=true">
                    <li></li>
                    <li></li>
                    <li></li>
                  </ul>
                </div>
                <transition
                  enter-active-class="animated zoomIn"
                  leave-active-class="animated zoomOut"
                >
                  <div class="shade" v-show="item.identifySub.shadeShow">
                    <div class="shade_con" @click="item.identifySub.shadeShow=false"></div>
                    <img
                      @click="onCollect(item,$event)"
                      class="img1"
                      :src="item.identifySub.shadeImg==false?require('../assets/images/btn_collect_normal@2x.png'):require('../assets/images/btn_collect_selected@2x.png')"
                      alt
                    />
                    <img
                      @click="onLike(item)"
                      class="img2"
                      :src="item.identifySub.shadeImg1==false?require('../assets/images/btn_good_normal@2x.png'):require('../assets/images/btn_good_selected@2x.png')"
                      alt
                    />
                    <div class="disabled1" v-show="item.identifySub.shadeImg1"></div>
                    <img
                      @click="item.identifySub.trampleShow = true,item.identifySub.shadeShow = false"
                      class="img3"
                      :src="item.identifySub.shadeImg2==false?require('../assets/images/btn_bad_normal@2x.png'):require('../assets/images/btn_bad_selected@2x.png')"
                      alt
                    />
                    <div class="disabled" v-show="item.identifySub.shadeImg2"></div>
                  </div>
                </transition>
                <transition
                  enter-active-class="animated zoomIn"
                  leave-active-class="animated zoomOut"
                >
                  <div class="trample" v-show="item.identifySub.trampleShow">
                    <p @click="onTreadCause($event,item)"></p>
                    <p @click="onTreadCause($event,item)"></p>
                    <p @click="onTreadCause($event,item)"></p>
                    <p
                      @click="item.identifySub.trampleShow = false,writeShow = true,secConObj=item"
                    ></p>
                  </div>
                </transition>
              </div>
            </div>
            <div class="tab_section1" v-show="!tabSection">
              <div class="tab_content" v-for="(item,index) of item.list" :key="index">
                <img @click="onSectionView(item)" :src="item.image" class="left" alt />
                <div class="right">
                  <div @click="onSectionView(item)" class="title">
                    <span>
                      {{item.title}}
                      <em v-show="item.updateShow"></em>
                    </span>
                  </div>
                  <p @click="onSectionView(item)">{{item.text}}</p>
                  <div class="bottom">
                    <img src="../assets/images/icon_visits@2x.png" alt />
                    <div class="tel">{{item.tel}}</div>
                    <img src="../assets/images/btn_good_normal@2x1.png" alt />
                    <div class="tel">{{item.sureCount}}</div>
                    <ul class="bo_r" @click="item.identifySub.shadeShow=true">
                      <li></li>
                      <li></li>
                      <li></li>
                    </ul>
                  </div>
                </div>
                <transition
                  enter-active-class="animated zoomIn"
                  leave-active-class="animated zoomOut"
                >
                  <div class="shade" v-show="item.identifySub.shadeShow">
                    <div class="shade_con" @click="item.identifySub.shadeShow=false"></div>
                    <img
                      @click="onCollect(item)"
                      class="img1"
                      :src="item.identifySub.shadeImg==false?require('../assets/images/btn_collect_normal@2x.png'):require('../assets/images/btn_collect_selected@2x.png')"
                      alt
                    />
                    <img
                      @click="onLike(item)"
                      class="img2"
                      :src="item.identifySub.shadeImg1==false?require('../assets/images/btn_good_normal@2x.png'):require('../assets/images/btn_good_selected@2x.png')"
                      alt
                    />
                    <div class="disabled1" v-show="item.identifySub.shadeImg1"></div>
                    <img
                      @click="item.identifySub.shadeImg2 = true,item.identifySub.trampleShow = true,item.identifySub.shadeShow = false"
                      class="img3"
                      :src="item.identifySub.shadeImg2==false?require('../assets/images/btn_bad_normal@2x.png'):require('../assets/images/btn_bad_selected@2x.png')"
                      alt
                    />
                    <div class="disabled" v-show="item.identifySub.shadeImg2"></div>
                  </div>
                </transition>
                <transition
                  enter-active-class="animated zoomIn"
                  leave-active-class="animated zoomOut"
                >
                  <div class="trample" v-show="item.identifySub.trampleShow">
                    <p @click="onTreadCause($event,item)"></p>
                    <p @click="onTreadCause($event,item)"></p>
                    <p @click="onTreadCause($event,item)"></p>
                    <p
                      @click="item.identifySub.trampleShow = false,writeShow = true,secConObj=item"
                    ></p>
                  </div>
                </transition>
              </div>
            </div>
          </van-tab>
        </van-tabs>
      </div>
    </section>
    <van-overlay :show="writeShow" z-index="300">
      <div class="submitInfo" @click.stop>
        <van-field
          v-model="treadMsg"
          rows="6"
          autosize
          type="textarea"
          maxlength="100"
          placeholder=""
          show-word-limit
        />
        <van-button
          type="primary"
          color="linear-gradient(270deg, #45A4B1 0%, #2A7875 100%)"
          block
          @click="onTreadIdea()"
        ></van-button>
        <img @click="writeShow = false,treadMsg = '',secConObj.identifySub.shadeImg2 = false" src="../assets/images/btn_close@1x.png" alt />
      </div>
    </van-overlay>
    <tabbar></tabbar>
  </div>
</template>
<script>
import BScroll from "better-scroll";
export default {
  name: "Home",
  components: {
    Tabbar: () => import("../components/Tabbar.vue")
  },
  data() {
    return {
      setCollectShow: false,
      navAttention: true,
      foldFlag: "",
      foldShow: true,
      tabSection: false,
      secTabActive: 0,
      secConObj: {},
      writeShow: false,
      treadMsg: "",
      navAttentionArr: [],
      navFootprintArr: [],
      headSwiper: [],
      secRecommendOne: {},
      secRecommendTwo: {},
      secRecommendThree: {},
      secSwiper: [],
      secTabContent:[],
      userInfoObj: {}
    };
  },
  created() {
    this.$nextTick(() => {
      this.ifLoginEquipment() //
    });
  },
  methods: {
    //
    viewUpdateTime(){
      let timer = setTimeout(()=>{
        clearTimeout(timer)
        this.getNavAttentionData(); //nav
        this.getNavFootprintData(); //nav
        this.getSectionView(); //section
        this.viewUpdateTime() //
      },60000)
    },
    //
    ifLoginEquipment() {
        var EquipmentMsg = navigator.userAgent;
        var TogoFlag = 0;
        var Agents = [
            "Android",
            "iPhone",
            "SymbianOS",
            "WindowsPhone",
            "iPad",
            "iPod"
        ];
        for (var i = 0; i < Agents.length; i++) {
            if (EquipmentMsg.indexOf(Agents[i]) > 0) {
            TogoFlag = 1;
            break;
            }
        }
        if (TogoFlag == 1) {
            this.getUserInfo() //
        }else{
          this.$router.push({ path: "", query: {} });
        }
    },
    //
    getUserInfo() {
      setTimeout(()=>{
          var that = this
          window["rh"] && window["rh"].getUserInfo({}, function(result) {
              alert(result);
              var resultObj = JSON.parse(result)
              let userInfoObj = {}
              let odeptidArr = resultObj.ODEPT_CODE_PATH.split("^")
              userInfoObj.aamid = resultObj.USER_LOGIN_NAME
              userInfoObj.deptid = resultObj.DEPT_CODE
              userInfoObj.odeptid = odeptidArr[odeptidArr.length-2]
              userInfoObj.user_mobile = resultObj.USER_MOBILE
              userInfoObj.user_name = resultObj.USER_NAME
              userInfoObj.user_post = resultObj.USER_POST
              userInfoObj.user_email = resultObj.USER_EMAIL
              userInfoObj.user_sex = resultObj.USER_SEX
              userInfoObj.cmpy_name = resultObj.CMPY_NAME
              userInfoObj.user_office_phone = resultObj.USER_OFFICE_PHONE
              userInfoObj.tdept_name = resultObj.TDEPT_NAME
              userInfoObj.user_birthday = resultObj.USER_BIRTHDAY
              that.$store.commit("userInfo",userInfoObj)
              that.userInfoObj = that.$store.state.userInfoObj
              alert(JSON.stringify(that.userInfoObj))
              setTimeout(()=>{
                that.browseRecord() //
                that.getHeadSwiper(); //header
                that.getNavAttentionData(); //nav
                that.getNavFootprintData(); //nav
                that.getBroadcast(); //
                that.getSectionRecommend(); //
                that.getSectionView(); //section
                that.viewUpdateTime()  //
              },0)
          }, function(err) {
          alert(err);
          }
        );
      },200)
    },
    //
    browseRecord(){
      this.$axios.post("/hopeUserLog_h/insertFourCh",{
        // aamid:"010203",
        "aamid": this.userInfoObj.aamid,
        "moduleid":-1,
        "logtime":new Date()
      })
    },
    //
    viewUpdate(arr){
      this.$axios.get(`/xxtx/py/reminder?aamid=${this.userInfoObj.aamid}`).then(res=>{
        if(res.data.errcode=="success" && res.status === 200){
          let updateArr = [...res.data.data]||[]
          updateArr.forEach(i=>{
            arr.forEach(item=>{
              item.list.forEach(item1=>{
                if(i===item1.moduleid){
                  item1.updateShow = true
                }
              })
            })
          })
        }        
      })
    },
    //
    navUpdate(arr){
      this.$axios.get(`/xxtx/py/reminder?aamid=${this.userInfoObj.aamid}`).then(res=>{
        if(res.data.errcode=="success" && res.status === 200){
          let updateArr = [...res.data.data]||[]
          updateArr.forEach(i=>{
            arr.forEach(item=>{
              if(i===item.moduleid){
                item.updateShow = true
              }
            })
          })
        }
      })
    },
    //nav
    personScroll() {
      this.$nextTick(() => {
        let width = (this.$refs.personItem.length + 1) * 80;
        this.$refs.personTab.style.width = width + "px";
        if (!this.scroll) {
          this.scroll = new BScroll(this.$refs.personWrap, {
            startX: 0,
            click: true,
            scrollX: true,
            // 
            scrollY: false,
            eventPassthrough: "vertical"
          });
        } else {
          this.scroll.refresh();
        }
      });
    },
    //
    onSearch() {
      this.$router.push({ path: "/search" });
      this.setCollectShow = false;
    },
    //
    onSetCollect() {
      this.$router.push({ path: "/myConcerns" });
    },
    //header
    getHeadSwiper() {
      this.$axios
        .post("/hopeImagebar/queryHopeImagebar", {
          // aamid: "010203",
          // deptid: "0010101032",
          // odeptid: "0010100003"
          "aamid": this.userInfoObj.aamid,
          "deptid": this.userInfoObj.deptid,
          "odeptid": this.userInfoObj.odeptid
        })
        .then(res => {
          if (res.status === 200 && res.data.status === "000000") {
            [...res.data.data].forEach(item => {
              if(item.imagebarurl === null||item.imagebarurl === ""){
                item.imagebarurl = "javascript:void(0)"
              }
            });
            this.headSwiper = [...res.data.data] || [];
          }
        });
    },
    //nav
    getNavAttentionData() {
      this.$axios
        .post("/hopeModule/queryWatchHopeModule", {
          // aamid: "010203"
          "aamid": this.userInfoObj.aamid
        })
        .then(res => {
          if (res.status === 200 && res.data.status === "000000") {
            this.navUpdate([...res.data.data.list])
            this.navAttentionArr = [...res.data.data.list] || [];
            this.foldFlag = res.data.data.mstatus;
            let timer = setTimeout(() => {
              if (timer) {
                clearTimeout(timer);
                this.personScroll();
                if (this.foldFlag === true) {
                  this.$refs.personItem.length <= 3
                    ? (this.foldShow = false)
                    : (this.foldShow = true);
                } else {
                  this.foldShow = true;
                }
              }
            }, 0);
          }
        });
    },
    //nav
    getNavFootprintData() {
      this.$axios
        .post("/hopeModule/queryMyFoot", {
          // aamid: "010203",
          "aamid": this.userInfoObj.aamid,
          "status": "1"
        })
        .then(res => {
          if (res.status === 200 && res.data.status === "000000") {
            this.navUpdate([...res.data.data])
            this.navFootprintArr = [...res.data.data] || [];
            let timer = setTimeout(() => {
              if (timer) {
                clearTimeout(timer);
                this.personScroll();
                if (this.foldFlag === true) {
                  this.$refs.personItem.length <= 3
                    ? (this.foldShow = false)
                    : (this.foldShow = true);
                } else {
                  this.foldShow = true;
                }
              }
            }, 0);
          }
        });
    },
    //nav
    onSwitchover() {
      this.navAttention = !this.navAttention;
      let timer = setTimeout(() => {
        if (timer) {
          clearTimeout(timer);
          this.personScroll();
          if (this.foldFlag === true) {
            this.$refs.personItem.length <= 3
              ? (this.foldShow = false)
              : (this.foldShow = true);
          } else {
            this.foldShow = true;
          }
        }
      }, 0);
    },
    //
    onNavTabView() {
      this.foldFlag = !this.foldFlag;
      this.$axios.post("/hopeModule/editModuleConf", {
        // aamid: "010203",
        "aamid": this.userInfoObj.aamid,
        "confvalue": this.foldFlag === false ? 0 : 1,
        "setname": ""
      })
      .then(res => {
        if (res.status === 200 && res.data.status === "000000") {
          let timer = setTimeout(() => {
            if (timer) {
              clearTimeout(timer);
              this.personScroll();
              if (this.foldFlag === true) {
                this.$refs.personItem.length <= 3
                  ? (this.foldShow = false)
                  : (this.foldShow = true);
              } else {
                this.foldShow = true;
              }
            }
          }, 0);
        }
      });
    },
    //nav
    onConcerns() {
      if (this.navAttention == true) {
        this.$router.push({ path: "/myConcerns" });
      } else {
        if(this.navFootprintArr.length>0){
          this.$router.push({ path: "/myFootprint" });
        }else{
          return false
        }
      }
    },
    //
    onBroadcast(item) {
      this.$router.push({
        path: "/broadcast",
        query: { name: item.broadcasttype }
      });
    },
    //
    getBroadcast() {
      this.$axios
        .post("/hopeBroadcast/queryHopeBroadcast", {
          // aamid: "010203",
          // deptid: "0010101032",
          // odeptid: "0010100003"
          "aamid": this.userInfoObj.aamid,
          "deptid": this.userInfoObj.deptid,
          "odeptid": this.userInfoObj.odeptid
        })
        .then(res => {
          if (res.status === 200 && res.data.status === "000000") {
            this.secSwiper = [...res.data.data] || [];
          }
        });
    },
    //
    onRecommendOne(){
      if(this.secRecommendOne.shortModuleUrl!=null&&this.secRecommendOne.shortModuleUrl!=""){
        window.location.href = this.secRecommendOne.shortModuleUrl
      }else if(this.secRecommendOne.shortModuleUrl==null||this.secRecommendOne.shortModuleUrl==""&&this.secRecommendOne.list!=null&&this.secRecommendOne.list!=""){
        this.$router.push({path:'/recommenddetail',query:{id:this.secRecommendOne.list,title:this.secRecommendOne.shortcutbarname}})
      }else{
        return false
      }
    },
    onRecommendTwo(){
      if(this.secRecommendTwo.shortModuleUrl!=null&&this.secRecommendTwo.shortModuleUrl!=""){
        window.location.href = this.secRecommendTwo.shortModuleUrl
      }else if(this.secRecommendTwo.shortModuleUrl==null||this.secRecommendTwo.shortModuleUrl==""&&this.secRecommendTwo.list!=null&&this.secRecommendTwo.list!=""){
        this.$router.push({path:'/recommenddetail',query:{id:this.secRecommendTwo.list,title:this.secRecommendTwo.shortcutbarname}})
      }else{
        return false
      }
    },
    onRecommendThree(){
      if(this.secRecommendThree.shortModuleUrl!=null&&this.secRecommendThree.shortModuleUrl!=""){
        window.location.href = this.secRecommendThree.shortModuleUrl
      }else if(this.secRecommendThree.shortModuleUrl==null||this.secRecommendThree.shortModuleUrl==""&&this.secRecommendThree.list!=null&&this.secRecommendThree.list!=""){
        this.$router.push({path:'/recommenddetail',query:{id:this.secRecommendThree.list,title:this.secRecommendThree.shortcutbarname}})
      }else{
        return false
      }
    },
    //
    getSectionRecommend() {
      this.$axios
        .post("/hopeShortcutBar/queryShortcutBar", {
          // aamid: "010203",
          // deptid: "0010101032",
          // odeptid: "0010100003"
          "aamid": this.userInfoObj.aamid,
          "deptid": this.userInfoObj.deptid,
          "odeptid": this.userInfoObj.odeptid
        })
        .then(res => {
          if (res.status === 200 && res.data.status === "000000") {
            this.secRecommendOne = res.data.data.list[0];
            this.secRecommendTwo = res.data.data.list[1];
            this.secRecommendThree = res.data.data.list[2];
          }
        });
    },
    //section
    onCollect(item,e) {
      item.identifySub.shadeImg = !item.identifySub.shadeImg;
      if (item.identifySub.shadeImg == false) {
        this.$axios
          .post("/hopeModule/deleteWatchHopeModule", {
            // aamid: "010203",
            "aamid": this.userInfoObj.aamid,
            "moduleid": item.moduleid
          })
          .then(res => {
            if (res.status === 200 && res.data.status === "000000") {
              this.getNavAttentionData();
              this.$toast({
                message: "",
                icon: require("../assets/images/btn_collect_normal@2x.png")
              });
            }
          });
      } else {
        this.$axios
          .post("/hopeModule/insertWatchHopeModule", {
            // aamid: "010203",
            "aamid": this.userInfoObj.aamid,
            "moduleid": item.moduleid
          })
          .then(res => {
            if (res.status === 200 && res.data.status === "000000") {
              this.getNavAttentionData();
              this.$toast({
                message: "",
                icon: require("../assets/images/btn_collect_selected@2x.png")
              });
            }
          });
      }
    },
    //section
    onLike(item) {
      this.$axios
        .post("/hopeComments/insertHopeComments", {
          // aamid: "010203",
          "aamid": this.userInfoObj.aamid,
          "moduleid": item.moduleid,
          "logtime": Date.parse(new Date()),
          "comments": 1,
          "description": ""
        })
        .then(res => {
          if (res.status === 200 && res.data.status === "000000") {
            item.identifySub.shadeImg1 = true;
            this.getSectionView();
          }
        });
    },
    //section
    onTreadCause(e, item) {
      this.$axios
        .post("/hopeComments/insertHopeComments", {
          // aamid: "010203",
          "aamid": this.userInfoObj.aamid,
          "moduleid": item.moduleid,
          "logtime": Date.parse(new Date()),
          "comments": 0,
          "description": e.target.innerHTML
        })
        .then(res => {
          if (res.status === 200 && res.data.status === "000000") {
            item.identifySub.trampleShow = false;
            item.identifySub.shadeImg2 = true;
          }
        });
    },
    //section
    onTreadIdea() {
      if(this.treadMsg){
        this.$axios
          .post("/hopeComments/insertHopeComments", {
            // aamid: "010203",
            "aamid": this.userInfoObj.aamid,
            "moduleid": this.secConObj.moduleid,
            "logtime": Date.parse(new Date()),
            "comments": 0,
            "description": this.treadMsg
          })
          .then(res => {
            if (res.status === 200 && res.data.status === "000000") {
              this.writeShow = false;
              this.secConObj.identifySub.shadeImg2 = true;
              this.treadMsg = "";
            }
          });
      }else{
