        this.secConObj.identifySub.shadeImg2 = true;
      }
    },
    //section
    onSectionView(item) {
      this.$axios
        .post("/hopeUserLog_h/insertUserLog_h", {
          // aamid: "010203",
          "aamid": this.userInfoObj.aamid,
          "moduleid": item.moduleid,
          "logtime": Date.parse(new Date())
        })
        .then(res => {
          if (res.status === 200 && res.data.status === "000000") {
            if(item.updateShow===true){
              this.$axios.get(`/xxtx/py/add-mark?aamid=${this.userInfoObj.aamid}&moduleid=${item.moduleid}&modulename=${item.title}`)
            }
            window.location.href = item.url;
          }
        });
    },
    //section
    getSectionView() {
      this.$axios
        .post("/hopeModule/searchMoudleByGroup", {
          // aamid: "010203",
          // deptid: "0010101032",
          // odeptid: "0010100003"
          "aamid": this.userInfoObj.aamid,
          "deptid": this.userInfoObj.deptid,
          "odeptid": this.userInfoObj.odeptid
        })
        .then(res => {
          if (res.status === 200 && res.data.status === "000000") {
            this.viewUpdate([...res.data.data.list])
            this.secTabContent = [...res.data.data.list] || [];
            this.tabSection = res.data.data.mstatus;
          }
        });
    },
    //sec
    onSecTabView() {
      this.tabSection = !this.tabSection;
      this.$axios
        .post("/hopeModule/searchMoudleByGroup", {
          // aamid: "010203",
          // deptid: "0010101032",
          // odeptid: "0010100003"
          "aamid": this.userInfoObj.aamid,
          "deptid": this.userInfoObj.deptid,
          "odeptid": this.userInfoObj.odeptid
        })
        .then(res => {
          if (res.status === 200 && res.data.status === "000000") {
            this.viewUpdate([...res.data.data.list])
            this.secTabContent = [...res.data.data.list] || [];
          }
        });
      this.$axios.post("/hopeModule/editModuleConf", {
        // aamid: "010203",
        "aamid": this.userInfoObj.aamid,
        "confvalue": this.tabSection === false ? 0 : 1,
        "setname": ""
      });
    },
  }
